* {
  padding: 0;
  margin: 0;
}
.App{
  height:100vh;
  width:100vw;
}
.chatParent {
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 0.1cm;

}

.chatBox {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.mainChat {
  display: flex;
  flex-direction: column;
  border: thin solid black;
  height: 15cm;
  width: 80vw;
  overflow: auto;
  scroll-behavior: smooth;
  padding-bottom:0.2cm;
  padding-left:0.2cm;
}

.userImg {
  width: 1cm;
  height: 1cm;
  border-radius: 50%;
}

.nameDiv {
  background-color: rgb(147, 234, 226);
  width: fit-content;
  padding: 0.1cm 0.3cm;
  border-radius: 6px;
}

.userName {
  font-size: 12px;
  color: rgb(91, 91, 91);
}

.leftMsg {
  display: flex;
  flex-direction: row;
  column-gap: 0.2cm;
  margin-top: 0.2cm;
}

.left {
  text-align: left;
  /* padding:0.5cm; */
}

.right {
  text-align: right;
  background-color: rgb(238, 242, 159);
  width: fit-content;
  padding: 0.1cm 0.3cm;
  border-radius: 6px;
}

.rightMsg {
  margin: 0.1cm;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;


}

.icon {
  width: 0.7cm;
  height: 0.7cm;
}

.inputField {
  display: flex;
  flex-direction: row;
  column-gap: 0.2cm;
}

.nav{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding:0.4cm 1cm;
}

.signout{
  height:1cm;
  width:1cm;
  cursor: pointer;
}

.msgContainer{
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  &:hover{
    .icon1{
      display: flex;
    }
  }
}

.icon1{
  height:0.5cm;
  width:0.5cm;
  cursor:pointer;
  display:none;
}

.HomePage{
  width:100vw;
  height:100%;
}

.mainContent{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  padding:1cm;
}

.login{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  row-gap: 1cm;
  border: thin solid rgb(183, 183, 183);
  box-shadow: 1px 2px 0.5px grey;
  border-radius: 12px;
  width:fit-content;
  padding:1cm;
}

.SignInButton{
  padding: 0.2cm 0.3cm;
  background-color: rgb(48, 125, 250);
  color: #ffffff;
  box-shadow: 0.5px 0.5px black;
  border:none;
  border-radius: 6px;
}

.logo, .userDiv{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  column-gap: 0.3cm;
}

.userName{
  font-size: 16px;
}

.heading{
  font-size: 20px;
  font-weight: bolder;
}